import React from "react";

const renderStatus = status => {
  switch (status) {
    case "PENDING":
      return <span className="text-warning font-weight-bold">ang ch</span>;
    case "FINISHED":
      return <span className="text-danger font-weight-bold">Kt Thc</span>;
    case "OPENING":
      return <span className="text-success font-weight-bold">ang M</span>;
    case "PLAYING":
      return <span className="text-warning font-weight-bold">ang chi</span>;

    default:
      return (
        <span className="text-warning font-weight-bold">Khng Xc nh</span>
      );
  }
};

export default renderStatus;
